*, :before, :after {
  box-sizing: border-box;
  border-width: 0;
}

html {
  font-family: Arial;
  line-height: 1.5;
}

h1 {
  font-size: inherit;
  font-weight: inherit;
  margin: 0;
}

body {
  line-height: inherit;
  background-color: #121213;
  margin: 0;
}

button {
  font-family: inherit;
  font-size: 100%;
  font-weight: inherit;
  line-height: inherit;
  color: inherit;
  text-transform: none;
  margin: 0;
  padding: 0;
}

.main {
  max-width: 32rem;
  margin: 0 auto;
}

.header {
  border-bottom: 1px solid #3a3a3c;
  justify-content: center;
  align-items: center;
  gap: 3rem;
  padding-top: .5rem;
  padding-bottom: .5rem;
  display: flex;
}

.header > h1 {
  color: #fff;
  font-family: Trebuchet MS, Lucida Sans Unicode, Lucida Grande, Lucida Sans, Arial, sans-serif;
  font-size: 2.25rem;
  font-weight: 800;
  line-height: 2.5rem;
}

.header > * {
  color: #fff;
  fill: #fff;
}

.header > a {
  align-items: center;
  gap: .5rem;
  display: flex;
}

.header > a:hover, a:focus {
  opacity: .8;
  transition: all .2s ease-in-out;
}

.tile-grid {
  width: 350px;
  height: 420px;
  grid-template-rows: repeat(6, minmax(0, 1fr));
  gap: .25rem;
  margin: 5rem auto;
  display: grid;
}

.tile-row {
  grid-gap: .25rem;
  grid-template-columns: repeat(5, minmax(0, 1fr));
  display: grid;
}

.tile-row > div {
  color: #fff;
  border: 2px solid #3a3a3c;
  justify-content: center;
  align-items: center;
  font-size: 2.25rem;
  font-weight: 600;
  line-height: 2.5rem;
  display: flex;
}

.keyboard {
  margin: auto;
}

.keyboard-row {
  color: #fff;
  width: 100%;
  gap: .5rem;
  margin-bottom: .5rem;
  display: flex;
}

.keyboard-row > button {
  height: 58px;
  background-color: #818384;
  border-radius: 4px;
  flex: 1;
}

.keyboard-row > button:hover, .keyboard-row > button:active {
  opacity: .85;
  cursor: pointer;
  transition: all .1s ease-in-out;
}

.keyboard-row #ENTER, #DELETE {
  flex: 1.5;
}

